.menu-item {
    min-width: 30%;
    height: 240px;
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid black;
    margin: 7.5px 15px;
    overflow: hidden;

    &:hover {
        cursor: pointer;

        & .background-image {

            transform: scale(2.0);
            transition: transform 3s cubic-bezier(0.25, 0.45, 0.45, 0.95);
            transform: ease-out;

        }

        & .content {
            opacity: 0.9;
        }
    }


    &.large {
        height: 380px;
    }

    &:first-child {
        margin-right: 7.5px;
    }

    &:last-child {

        margin-left: 8px;


    }

    .background-image {
        width: 100%;
        height: 100%;
        background-position: center;
        background-size: cover;

    }

    .content {
        height: 90px;
        padding: 0 25px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        border: 1px solid black;
        background-color: white;
        opacity: 0.7;
        position: absolute;

        .title {
            font-weight: bold;
            margin-bottom: 4px;
            font-size: 22px;
            color: #4a4a4a;

            .subtitle {
                font-weight: lighter;
                font-size: 16px;

            }


        }

    }


}